import { post } from '~/plugins/axios'

// 获取剧集分类列表
const classifyList = () => post('/vod/classify/list.action', {})

// 获取剧集查询结果列表
const videoListResult = ({ uuidList }) =>
  post('/api/vipActivity/getActivityInfo.action', {
    uuidList,
    rows: 10,
    startRow: 1,
    isVip: -1,
    orderType: 1,
  })

const getVodInfo = ({ vodUuid }) =>
  post('/api/vod/getVodInfo.action', {
    vodUuid,
    num: 1,
  })

export { classifyList, videoListResult, getVodInfo }
