import { getVodInfo } from '../../api/index.js'
export default {
  async asyncData({ params, error }) {
    try {
      const { data } = await getVodInfo({
        vodUuid: params.id,
      })
      return {
        vodInfo: data,
        title: data.name,
        introduction: data.introduction,
      }
    } catch (e) {
      error({ message: 'User not found', statusCode: 404 })
    }
  },
  data() {
    return {
      vodInfo: '',
      title: 'abcd',
      introduction: '',
    }
  },
  head() {
    return {
      title: this.title,
      meta: [
        {
          hid: 'keywords',
          name: 'keywords',
          content: 'TVB，粤语，港剧，高清，免费，在线点播，翡翠台',
        },
        {
          hid: 'description',
          name: 'description',
          content: this.vodInfo.introduction,
        },
      ],
    }
  },
}
